.carousel{
    margin-top: px_to_vw_desktop(195);
    display: grid;
    grid-template-columns: 2fr 1fr;
    height: px_to_vw_desktop(780);
    overflow: hidden;
    .hidden{
        display: none;
    }
    &__images{
        position: relative;
        height: 100%;
        width: 100%;
        &__block{
            position: absolute;
            margin-top: -48px;
            top: px_to_vw_desktop(248);
            left: 0;
            height: px_to_vw_desktop(416);
            width: px_to_vw_desktop(324);
            @include windowMax1700p{
                margin-top: -45px;
            }
            @include windowMax1280p{
                margin-top: -41px;
            }
            p{
                @include mulish500();
                text-transform: uppercase;
                color: #B58C67;
                font-size: 16px;
                line-height: 1.5em;
                letter-spacing: 0.08em;
                @include windowMax1700p{
                    font-size: 14px;
                }
                @include windowMax1280p{
                    font-size: 12px;
                }
            }
            span{
                @include mulish400();
                font-size: 24px;
                line-height: 1.33333em;
                letter-spacing: -0.04em;
                @include windowMax1700p{
                    font-size: 19px;
                }
                @include windowMax1280p{
                    font-size: 14px;
                }
            }
            img{
                margin: 24px 0 16px 0;
                width: 100%;
            }
        }
        .active{
            left: 100%;
            top:   0;
            width: px_to_vw_desktop(600);
            transform: translateX(-100%);
        }
        display: flex;
    }
    &__description{
        position: relative;
        display: flex;
        flex-direction: column;
        padding-top: px_to_vw_desktop(200);
        margin-left: px_to_vw_desktop(150);
        margin-right: px_to_vw_desktop(88);
        p{
            @include mulish400();
            font-size: 16px;
            line-height: 1.5em;
            letter-spacing: 0.02em;
            color: #3E3025;
            @include windowMax1700p{
                font-size: 14px;
            }
            @include windowMax1280p{
                font-size: 12px;
            }

        }
        span{
            @include mulish500();
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #B58C67;
        }
        h1{
            @include tenor();
            font-size: 32px;
            line-height: 1.25em;
            letter-spacing: -0.04em;
            text-transform: uppercase;
            color: #B58C67;
            padding-top: 1em;
            padding-bottom: 0.5em;
            @include windowMax1700p{
                font-size: 26px;
            }
            @include windowMax1280p{
                font-size: 19px;
            }
        }
        &__controls{
            position: absolute;
            bottom: 0;
            &__elements{
                p{
                    font-size: 32px;
                    line-height: 1em;
                    color: #B58C67;
                    @include windowMax1700p{
                        font-size: 30px;
                    }
                    @include windowMax1280p{
                        font-size: 28px;
                    }
                }
                display: flex;
                align-items: center;
            }
            &__arrows{
                padding-left: 24px;
                img{
                    width: 56px;
                    height: 56px;
                    @include windowMax1700p{
                        width: 52px;
                        height: 52px;
                    }
                    @include windowMax1280p{
                        width: 48px;
                        height: 48px;
                    }
                }
            }
        }
    }
}