.quote{
    margin: 0 px_to_vw_desktop(276);
    padding: px_to_vw_desktop(310) 0;
    display: flex;
    justify-content: space-between;
    &__text{
        max-width: px_to_vw_desktop(672);
        position: relative;
        p{
            font-size: 16px;
            line-height: 1.5em;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #B58C67;
            @include windowMax1700p{
                font-size: 14px;
            }
            @include windowMax1280p{
                font-size: 12px;
            }
        }
        h1{
            font-size: px_to_vw_desktop(32);
            line-height: 1.5em;
            letter-spacing: -0.04em;
            text-transform: uppercase;
            color: #B58C67;
            margin-top: px_to_vw_desktop(30);
        }
        img{
            width: px_to_vw_desktop(36);
            height: px_to_vw_desktop(25);
            margin-top: px_to_vw_desktop(80);
        }
        &__bottom{
            position: absolute;
            bottom: 0;
        }
    }
    &__picture{
        img{
            height: px_to_vw_desktop(664);
            aspect-ratio: 447/664;
        }
    }
}