.residental{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #0B1A25;
    &__rectangle{
        width: px_to_vw_desktop(174);
        height: 1px;
        background-color: #908F8B33;
        //margin-top: px_to_vw_desktop(205);
    }
    p{
        @include tenor();
        font-size: px_to_vw_desktop(32);
        line-height: 1.25em;
        text-align: center;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        color: #B58C67;
        padding-top: px_to_vw_desktop(38);
    }
    &__content{
        display: flex;
        box-sizing: border-box;
        padding: 0 px_to_vw_desktop(204);
        padding-top: px_to_vw_desktop(140);
        padding-bottom: px_to_vw_desktop(92);
        width: 100%;
        &__left, &__right{
            width: 50%;
            display: flex;
            flex-direction: column;
            box-sizing: border-box;
            padding: 0 82px;
            gap: 102px;
        }
        &__right{
            padding-top: 110px;
        }
        &__item{
            img{
                width: 100%;
            }
            h1{
                @include tenor();
                font-size: 24px;
                line-height: 1.333333em;
                letter-spacing: -0.04em;
                text-transform: uppercase;
                color: #B58C67;
                padding-top: 24px;
                border-top: solid 1px #908F8B33;
                margin-top: 32px; 
            } 
            &__text{
                p{
                    @include mulish500();
                    font-size: 14px;
                    line-height: 28px;
                    letter-spacing: 0.08em;
                    text-transform: uppercase;
                    color: #6F6862;
                    padding-top: 4px;
                    text-align: start;
                }
            }
        }
    }
    &__arrow{
        padding-bottom: px_to_vw_desktop(220);
        button{
            width: px_to_vw_desktop(120);
            height: px_to_vw_desktop(120);
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #B58C67;
            transition: 0.3s background-color;
            cursor: pointer;
            &:hover{
                background-color: #B58C67;
            }
            img{
                width: px_to_vw_desktop(48);
                height: px_to_vw_desktop(48);
            }
        }
        p{
            @include mulish500();
            font-size: 16px;
            line-height: 30px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: #908F8B;
            display: none;
        }
    }
}