.propety {
    background: #0B1A25;
    padding-top: px_to_vw_desktop(300);
    padding-bottom: px_to_vw_desktop(220);
    display: flex;
    flex-direction: column;
    align-items: center;
    p {
        @include mulish500();
        font-size: 16px;
        line-height: 1.5em;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #B58C67;

        @include windowMax1700p {
            font-size: 14px;
        }

        @include windowMax1280p {
            font-size: 12px;
        }
    }

    h1 {
        @include mulish500();
        max-width: px_to_vw_desktop(915);
        font-size: px_to_vw_desktop(64);
        padding-top: px_to_vw_desktop(40);
        line-height: 1.15625em;
        text-align: center;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        color: #B58C67;
    }
}